import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource'
import Main from '@/components/Main'
import MainEN from '@/components/Main-en'
import Browser from '@/components/Browser'
import BrowserEN from '@/components/Browser-en'
import ScatterJS from 'scatterjs-core'
import ScatterEOS from 'scatterjs-plugin-eosjs'
// import Vuex from 'vuex'

ScatterJS.plugins( new ScatterEOS() )

Vue.use(Router)
Vue.use(VueResource)


export default new Router({
  routes: [
    {path: '/',name: 'Main',component: Main},
    {path: '/index-en', name: 'MainEN', component: MainEN},
    {path: '/browser', name: 'Browser', component: Browser},    
    {path: '/browser-en', name: 'BrowserEN', component: BrowserEN}
  ]
})



